import { useParams, Link } from "react-router-dom";
import axios, * as others from "axios";
import { useState } from "react";
import { GetIpfsUrlFromPinata } from "./utils";
import { useNavigate } from "react-router";
import {ethers} from "ethers";
import "../css/NFTPage.css";
import logo from "../비담 로고.png";


// NFtPage는 NFT의 상세 정보를 보여주는 페이지입니다.
export default function NFTPage() {
  const navigate = useNavigate();
  const [data, updateData] = useState({});
  const [receiver, setReceiver] =useState("");
  const [dataFetched, updateDataFetched] = useState(false);
  const [currAddress, updateCurrAddress] = useState("0x");
  const [isActiveSendNftModal, setIsActiveSendNftModal] = useState(false);
  const [isAuctionSectionActive, setIsAuctionSectionActive] = useState(false);
  const [formParams, updateFormParams] = useState({
    host: "",
    token_id:"",
    title:"",
    date: "",
    start_bid:"",
    image:"",
  });

  async function disableButton(id) {
    const listButton = document.getElementById(id);
    listButton.disabled = true;
    listButton.style.backgroundColor = "grey";
    listButton.style.opacity = 0.2;
  }

  // 블록체인 네트워크와 통신이 끝나면 버튼을 다시 활성화 시키는 함수입니다.
  async function enableButton(id) {
    const listButton = document.getElementById(id);
    listButton.disabled = false;
    listButton.style.backgroundColor = "rgb(29 58 42/var(1))";
    listButton.style.opacity = 1;
  }

  // TokenId에 해당하는 NFT의 정보를 가져오는 함수입니다. - TokenId는 NFT의 고유한 ID입니다.
  async function getNFTData(tokenId) {
    // 블록체인에 연결하기 위한 코드입니다.
    const ethers = require("ethers");

    const contractAddress = "0x295BC890632A6935691802A2F8d70E038c4F713f";
    const contractABI = require("../NFT.json");

    const provider = new ethers.BrowserProvider(window.ethereum);
    const signer = await provider.getSigner();
    const addr = await signer.getAddress();

    let contract = new ethers.Contract(
      contractAddress,
      contractABI,
      signer
    );

    // tokenId에 해당하는 nft의 메타데이터가 저장되어있는 pinata IPFS 주소를 가져옵니다.
    var tokenURI = await contract.tokenURI(tokenId);

    // tokenId에 해당하는 nft의 정보를 가져옵니다.
    const listedToken = await contract.getListedTokenForId(tokenId);

    // pinata IPFS 주소를 일반 IPFS 주소로 바꿔줍니다.
    tokenURI = GetIpfsUrlFromPinata(tokenURI);

    // IPFS 주소에 저장되어 있는 JSON 파일을 가져옵니다(JSON 파일에는 nft의 메타데이터가 저장되어 있습니다.)
    let meta = await axios.get(tokenURI);
    meta = meta.data;

    // 가져온 정보들을 item에 담습니다.
    let item = {
      tokenId: tokenId,
      owner: listedToken.owner,
      image: meta.image,
      name: meta.name,
      description: meta.description,
    };

    // item에 저장된 정보들을 data에 업데이트합니다.
    updateData(item);
    updateDataFetched(true);
    updateCurrAddress(addr);
  }

  const goToProfile = () => {
    navigate("/profile");
  }

  async function transferNFT(e) {
    e.preventDefault();
    const {ethers} = require("ethers");

    disableButton("transfer-button");
    disableButton("cancel-button");
    const contractAddress = "0x295BC890632A6935691802A2F8d70E038c4F713f";
    const contractABI = require("../NFT.json");

    const provider = new ethers.BrowserProvider(window.ethereum);
    const signer = await provider.getSigner();

    const contract = new ethers.Contract(
    contractAddress,
    contractABI,
    signer
    );

    let transaction = await contract.transferNFT(data.owner,receiver,data.tokenId);
    await transaction.wait()

    enableButton("transfer-button");
    enableButton("cancel-button");
    goToProfile();
  }

  async function sendBidInfo(e) {
    e.preventDefault();
    const {token_id, start_bid} = formParams;
    const {ethers} = require("ethers");

    const contractAddress = "0x295BC890632A6935691802A2F8d70E038c4F713f";
    const contractABI = require("../NFT.json");

    const provider = new ethers.BrowserProvider(window.ethereum);
    const signer = await provider.getSigner();

    const contract = new ethers.Contract(
      contractAddress,
      contractABI,
      signer
      );

    let transaction = await contract.start(token_id,ethers.formatEther(start_bid));
    await transaction.wait()

    await axios.post("https://port-0-apis-ac2nll962w8w.sel3.cloudtype.app/api/bidInfo", {info : formParams})
    .then((res) => {
      console.log(res.data);
      });
    
    goToProfile();
    
  }

  // NFTPage의 URL에 있는 tokenId를 가져옵니다(NFTPage의 URL은 /nft/:tokenId 입니다.)
  const params = useParams();
  const tokenId = params.tokenId;

  // tokenId에 해당하는 NFT의 정보를 가져옵니다.
  if (!dataFetched) getNFTData(tokenId);
  if (typeof data.image == "string")
    data.image = GetIpfsUrlFromPinata(data.image);

  
  return (
    <div className="flex  flex-col items-center justify-center bg-cream100 min-h-screen">
      {/* 다른 페이지로 이동하기 위한 버튼들입니다. */}
      {/* <Link to="/profile"><div className='back'>👤</div></Link>
            <Link to="/marketplace"><div className='back'>🛒</div></Link>
            <Link to="/mint"><div className='back'>⚒️</div></Link>
            <Link to="/"><img src={logo} className='logo'/></Link> */}

      {/* NFT의 정보를 보여주는 부분입니다. */}

      {/* SEND NFT 모달 영역입니다.  */}
      {isActiveSendNftModal && (
        <div className="modal-container">
          <div className="modal-content w-fit  rounded-none border-[2px] border-brand100">
            <div className="flex flex-col h-full ">
              <h3 className="text-brand100  text-left mb-2 lg:text-[18px] font-semibold">
                SEND TO :
              </h3>
              <input
                type="text"
                className="border-[2px] pl-2 text-mono70 text-[16px] border-mono100 rounded-[20px] lg:w-[280px] w-[220px] h-[30px]"
                value={receiver}
                onChange={(e)=>setReceiver(e.target.value)}
              />
              {console.log(receiver)}
              <div className="flex w-full content-between justify-between mt-6">
                <button
                  id="transfer-button"
                  type="button"
                  className="w-[48%]  h-[32px] text-[16px]  bg-brand100 text-cream100 rounded-[50px]"
                  onClick={transferNFT}
                >
                  SEND
                </button>
                <button
                  id="cancel-button"
                  type="button"
                  className="w-[48%]  h-[32px] text-[16px] bg-brand100 text-cream100 rounded-[50px]"
                  onClick={() => {
                    setIsActiveSendNftModal(false);
                  }}
                >
                  CANCEL
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* 다경 작업 (시작) */}
      <div className="flex flex-col lg:bg-green100 bg-none lg:bg-opacity-10 lg:p-10 p-2 rounded-md items-center  py-3 my-[40px] lg:my-[80px] min-h-screen">
        <div className="w-full">
          <div className="text-green100 lg:text-[26px] text-[20px] font-bold mt-10">
            {data.name}
          </div>
          <div className="text-mono70 lg:text-[18px]"> {data.description}</div>
        </div>

        <hr className="border border-brand100 w-full my-5" />
        <img
          src={data.image}
          alt="nftimg"
          className="w-[310px] h-[285px] lg:w-[560px] lg:h-[480px] rounded-[20px]"
        />

        <div className="flex flex-col lg:flex-row space-y-[30px] lg:space-y-0 lg:space-x-2 space-x-0 mt-[40px]">
          <button
            type="button"
            className="w-[310px] h-[39px] rounded-[50px] text-cream100 text-center bg-brand100 border border-emerald-400 hover:font-semibold"
            onClick={() => {
              setIsAuctionSectionActive(!isAuctionSectionActive);
              updateFormParams({...formParams, host:data.owner, token_id:data.tokenId, title:data.name, image:data.image})
            }}
          >
            {isAuctionSectionActive
              ? "❌ CLOSE AN AUCTION "
              : "OPEN AN AUCTION"}
          </button>

          <button
            type="button"
            className="w-[310px] h-[39px] rounded-[50px] text-cream100 text-center bg-brand100 border border-emerald-400 hover:font-semibold"
            onClick={() => {
              setIsActiveSendNftModal(true);
            }}
          >
            SEND NFT
          </button>
        </div>

        {/* Auction 모달창 입니다.   */}
        {isAuctionSectionActive && (
          <div className="lg:max-w-[430px] lg:max-h-[400px] max-w-[280px] max-h-[280px] w-full h-full border border-black relative mt-10  p-5 bg-brand100 mb-10">
            <div className="flex bg-white bg-opacity-30 flex-col p-7 text-cream100">
              <div className="absolute -top-[16px] left-[10px] w-[100px] h-[30px] p-2.5 bg-zinc-800 rounded-[50px] border border-emerald-400 justify-center items-center gap-2.5 inline-flex">
                <div className="text-center text-white text-xs font-semibold tracking-tight">
                  AUCTION
                </div>
              </div>
              <div>
                <div className="flex  content-between justify-between">
                  <h3 className="font-semibold text-[10px] lg:text-[16px] ">
                    경매 시작 시간
                  </h3>
                  <div className="flex items-center text-[10px] lg:text-[16px] w-[60%]">
                    <div className="flex justify-center">
                      <input
                        type="datetime-local"
                        className="text-cream100 rounded-sm border-b lg:max-w-[190px] max-w-[125px] w-full border-cream100 bg-white bg-opacity-0"
                        value={formParams.date}
                        onChange={(e)=>
                        updateFormParams({...formParams, date:e.target.value})}
                      />
                      {console.log(formParams.date)}
                    </div>
                  </div>
                </div>

                <div className="flex content-between justify-between mt-3 text-[10px] lg:text-[16px]">
                  <h3 className="font-semibold ">시작가</h3>
                  <div className="flex">
                    <input
                      type="text"
                      className="border-b border-cream100 ml-3 bg-white bg-opacity-0 "
                      value={formParams.start_bid}
                      onChange={(e) =>
                        updateFormParams({ ...formParams, start_bid:e.target.value})}
                    />
                    <span className="ml-2">ETH</span>
                  </div>
                </div>
              </div>
              <div className="w-full flex items-center justify-center">
                <button
                  type="button"
                  className="bg-black hover:text-yellow-100 text-cream100 text-[10px] lg:text-[12px] font-semibold p-2 rounded-md mt-7"
                  onClick={sendBidInfo}
                >
                  OPEN AN AUCTION
                </button>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* 다경 작업 (끝) */}

      {/* <div className="Box">
        <img src={data.image} alt="" className="nft-img" />
        <div className="text-xl ml-20 space-y-8 text-white shadow-2xl rounded-lg border-2 p-5">
          <div className="Text">ID: #{data.tokenId}</div>
          <div className="Text">Name: {data.name}</div>
          <div className="Text">Description: {data.description}</div>
          <div className="Text">
            Owner: <span className="text-sm">{data.owner}</span>
          </div>
        </div>
      </div> */}
    </div>
  );
}