import axios, * as others from 'axios';
import Header from "./layout/Header";
import metamask from "../common/images/metamask.png";
import mainbg from "../common/images/bg-image.png";

export default function Login() {

  const handleConnect = async (e) => {
    e.preventDefault();

    await window.ethereum
      .request({
        method: "eth_requestAccounts",
      })
      .then(async (accounts) => {
        // UNITty 상에서 ID가 들어오면 이를 metamask 계정과 쌍으로 묶어 DB에 저장할 수 있도록 하는 코드입니다.
        await axios.post("https://port-0-apis-ac2nll962w8w.sel3.cloudtype.app/api/connectIDs", {address: accounts[0]})
        .then((res) => {console.log(res.data)});
        console.log(accounts);

        window.location.replace('/')
      })
      .catch((err) => {
        console.log(err);
      });
  };


  return (
    <div className=" bg-mono70 bg-opacity-20 ">
      <div className="flex">
        <div className="w-[50%] lg:block hidden"> </div>
        <div
          className="flex items-center lg:w-[50%] w-screen h-screen lg:justify-center   flex-col  "
          style={{
            backgroundImage: `url(${mainbg})`,
            backgroundRepeat: "no-repeat",
          }}
        >
          <div className="shadow-xl rounded-xl p-5 border   border-gray-400 bg-cream100 lg:bg-opacity-60 bg-opacity-80 flex flex-col items-center justify-center lg:w-[80%] w-[90%] lg:mt-0 mt-[40%] h-[25%] ">
            <h3 className="lg:text-[32px] mb-5 font-semibold text-brand100">
              메타마스크 로그인
            </h3>
            <button
              className="shadow-xl flex items-center justify-center lg:max-w-[320px] p-3 rounded-xl w-full bg-green60"
              onClick={handleConnect}
            >
              <img src={metamask} width="30px" height="30px" />
              <span className="ml-3 text-cream100 lg:text-[24px] font-semibold ">
                Connect Wallet
              </span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
