import { useState, useEffect } from "react";
import detectEthereumProvider from "@metamask/detect-provider/dist/detect-provider";
import axios, * as others from "axios";
import { formatBalance, formatChainAsNum } from "./additional";
import { Link, useNavigate } from "react-router-dom";
import "../css/Metamask.css";
import BankSVG from "./common/svg/BankSVG";
import InfoSVG from "./common/svg/InfoSVG";
import CoinSVG from "./common/svg/CoinSVG";
import PersonSVG from "./common/svg/PersonSVG";
import Header from "./layout/Header";
import MarketSVG from "./common/svg/MarketSVG";

// 맨처음에 보이는 홈페이지라고 생각하시면 됩니다.
// 계정을 연결하고, 연결된 계정의 정보를 보여줍니다.
// 그리고 test를 위해 mint, profile, marketplace로 이동할 수 있는 버튼이 있습니다.
export default function () {
  const [hasProvider, setHasProvider] = useState(null);
  const initialState = { accounts: [], balance: "", chainId: "" };
  // wallet은 연결된 계정의 정보를 담고 있습니다.
  const [wallet, setWallet] = useState(initialState);
  const [info,setinfo] = useState({});

  const [isConnecting, setIsConnecting] = useState(false);
  const [error, setError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  // 계정을 매번 연결하지 않아도 되게 하기 위해, useEffect를 사용했습니다.
  useEffect(() => {
    const refreshAccounts = (accounts) => {
      if (accounts.length > 0) {
        updateWallet(accounts);
      } else {
        setWallet(initialState);
      }
    };

    const refreshChain = (chainId) => {
      setWallet((wallet) => ({ ...wallet, chainId }));
    };

    const getProvider = async () => {
      const provider = await detectEthereumProvider({ silent: true });
      setHasProvider(Boolean(provider));

      if (provider) {
        const accounts = await window.ethereum.request({
          method: "eth_accounts",
        });
        refreshAccounts(accounts);
        window.ethereum.on("accountsChanged", refreshAccounts);
        window.ethereum.on("chainChanged", refreshChain);
      }
    };

    getProvider();

    async function getInfos(){
      const infos = await axios.get("https://port-0-apis-ac2nll962w8w.sel3.cloudtype.app/api/getResult")
      console.log(infos.data);
      setinfo(infos.data);
    };
    
    getInfos();

    return () => {
      window.ethereum?.removeListener("accountsChanged", refreshAccounts);
      window.ethereum?.removeListener("chainChanged", refreshChain);
    };
  }, []);

  // 연결된 wallet의 정보를 가져옵니다(주소, 잔액, chainId)
  const updateWallet = async (accounts) => {
    const balance = formatBalance(
      await window.ethereum.request({
        method: "eth_getBalance",
        params: [accounts[0], "latest"],
      })
    );
    const chainId = await window.ethereum.request({
      method: "eth_chainId",
    });
    setWallet({ accounts, balance, chainId });
  };

  // 계정을 연결하는 함수입니다.
  // 버튼을 누르면 matamask와 연결할 수 있도록 합니다.
  // const handleConnect = async () => {
  //   setIsConnecting(true);
  //   await window.ethereum.request({
  // method: "eth_requestAccounts",
  //   })
  //   .then(async (accounts) => {
  //       setError(false);
  //       updateWallet(accounts);
  //       // Unity 상에서 ID가 들어오면 이를 metamask 계정과 쌍으로 묶어 DB에 저장할 수 있도록 하는 코드입니다.
  //       await axios.post("http://localhost:8080/api/connectIDs", {address: accounts[0]})
  //       .then((res) => {console.log(res.data)});
  //   })
  //   .catch((err) => {
  //       setError(true);
  //       setErrorMessage(err.message);
  //   })
  //   setIsConnecting(false);
  //   };

  const disableConnect = Boolean(wallet) && isConnecting;

  return (
    <div className="bg-mono70 bg-opacity-10">
      <div className="user-info-container">
        <div className="lg:mt-[120px] mt-[60px]">
          <div className="flex items-center justify-center">
            <div className="user-wallet-info">
              <div>
                {!hasProvider && <h2>injected Provider "DOES NOT" Exist</h2>}

                {
                  /* { window.ethereum?.isMetaMask && wallet.accounts.length < 1 &&  /* Updated */
                  // <button disabled={disableConnect} onClick={handleConnect}>Connect MetaMask</button>
                }

                {wallet.accounts.length > 0 && (
                  <div className="wallet-info-content">
                    <div className="user-info-title">USER</div>
                    <h3 className="text-brown80 lg:text-2xl">Wallet Address</h3>
                    <h3 className="text-brown80 text-[10px] lg:text-lg">
                      {wallet.accounts[0]}
                    </h3>

                    <div className="flex justify-between mt-10 space-x-6 ">
                      <div className="bg-cream100 rounded-md flex relative lg:w-[50%] w-full items-center justify-center">
                        <div className="rounded-full bg-pink100 lg:w-[40px] lg:h-[40px] w-[32px] h-[32px] flex items-center justify-center absolute bottom-[80%] z-10 right-[88%]">
                          <BankSVG width="24px" height="24px" />
                        </div>
                        <div className="flex flex-col lg:p-2">
                          <h4 className="lg:text-xl text-[10px] text-brand100">
                            Wallet Balance
                          </h4>
                          <h5 className="lg:text-3xl font-semibold ">
                            {wallet.balance}
                          </h5>
                        </div>
                      </div>

                      <div className="bg-cream100 lg:w-[50%] rounded-md h-full  w-full relative lg:p-5 p-3 flex items-center justify-center">
                        <div className="rounded-full bg-green100 lg:w-[36px] lg:h-[36px] w-[32px] h-[32px] flex items-center justify-center absolute bottom-[80%] z-10 right-[88%]">
                          <InfoSVG width="24px" height="24px" />
                        </div>
                        <div className="flex flex-col">
                          <h4 className="lg:text-xl text-[10px] font-semibold text-brand100 ">
                            Hex ChainId
                          </h4>
                          <h5 className="text-[10px] lg:text-lg">
                            {wallet.chainId}
                          </h5>
                          <h4 className="lg:text-lg text-[10px] font-semibold text-brand100">
                            Numeric ChainId
                          </h4>
                          <h5 className="text-[10px] lg:text-lg ">
                            {formatChainAsNum(wallet.chainId)}
                          </h5>
                        </div>
                      </div>
                    </div>
                  </div>
                )}

                {wallet.accounts.length == 0 && (
                  <div>
                    <h3 className="lg:text-[26px] text-brand100 font-semibold text-center">
                      Welcome!
                    </h3>
                    <div className="lg:text-[22px] mt-5 text-brown100 lg:w-[360px] text-center">
                      지갑 로그인을 진행해주세요
                    </div>
                    <button
                      type="button"
                      className="underline text-center w-full mt-7 lg:text-[20px] hover:text-pink100"
                      onClick={() => {
                        navigate("/login");
                      }}
                    >
                      로그인 바로가기
                    </button>
                  </div>
                )}

                {error /* New code block */ && (
                  <div onClick={() => setError(false)}>
                    <strong>Error:</strong> {errorMessage}
                  </div>
                )}
              </div>
            </div>
          </div>

          <div className="metamask-button-list">
            <Link to="mint">
              <button>
                <CoinSVG width="46px" height="46px" />
                <span className="ml-1"> MINT NFT </span>
              </button>
            </Link>
            <Link to="profile">
              <button>
                <PersonSVG width="38px" height="38px" />
                <span> MY PROFILE</span>
              </button>
            </Link>
            <Link to={`bidding/${info.nft_token_id}`}>
              <button>
                <MarketSVG width="36px" height="36px" />
                <span className="ml-5"> PAYMENT</span>
              </button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
