import { BrowserRouter as Router, Link } from "react-router-dom";
import { GetIpfsUrlFromPinata } from "./utils";

// NFTTile은 NFT의 간단한 정보를 보여주는 타일입니다.
// 상세페이지가 NFTPage라면, 간단한 정보를 보여주는게 NFTTile입니다.
function NFTTile(data) {
  // NFTTile을 클릭하면 해당 NFT의 상세페이지로 이동할 수 있도록 NFTPage의 URL을 저장해줍니다.
  const newTo = {
    pathname: "/nftPage/" + data.data.tokenId,
  };

  const IPFSUrl = GetIpfsUrlFromPinata(data.data.image);

  return (
    // NFTTile을 클릭하면 해당 NFT의 상세페이지로 이동할 수 있도록 Link를 사용했습니다.
    <Link to={newTo} tokenId={data.data.tokenId} className="Tile">
      <div class="lg:max-w-[400px] max-w-[280px] w-full m-3 ">
        <div class="h-full border-2 border-gray-200 border-opacity-60 rounded-lg overflow-hidden">
          <img
            class="lg:h-[360px] h-[280px] w-full object-cover object-center"
            src={IPFSUrl}
            alt="NFT photo"
          />
          <div class="p-6  bg-[#BEC2C8] h-[40%]  text-cream100">
            <h2 class="tracking-widest text-sm font-medium text-gray-400 mb-1">
              MY NFT
            </h2>
            <h1 class="title-font text-xl font-medium text-gray-900 mb-3">
              {data.data.name}
            </h1>
            <p class="mb-3">{data.data.description}</p>
          </div>
        </div>
      </div>
    </Link>
  );
}

export default NFTTile;
