import React from "react";
import { useNavigate } from "react-router";

const Header = () => {
  const navigate = useNavigate();
  return (
    <header
      class="text-gray-600 "
      style={{
        backgroundImage: "linear-gradient(to right, #679277 -5%, #f0c7bd)",
      }}
    >
      <div class=" mx-[2%] flex flex-wrap p-6 flex-col md:flex-row items-center">
        <nav class="flex lg:w-2/5 flex-wrap items-center lg:text-xl  text-sm text-cream100 cursor-pointer">
          <a
            class="mr-5 hover:text-gray-900"
            onClick={() => {
              navigate("/mint");
            }}
          >
            MINT NFT
          </a>
          <a
            class="mr-5 hover:text-gray-900"
            onClick={() => {
              navigate("/profile");
            }}
          >
            MY PROFILE
          </a>
        </nav>
        <a class="flex order-first lg:order-none lg:w-1/5 title-font font-medium items-center text-gray-900 lg:items-center lg:justify-center mb-4 md:mb-0">
          <span
            class="ml-3 text-3xl text-cream100 cursor-pointer"
            onClick={() => {
              navigate("/");
            }}
          >
            BID:AM
          </span>
        </a>
      </div>
    </header>
  );
};

export default Header;
