import { useParams, Link } from "react-router-dom";
import axios, * as others from "axios";
import { useState } from "react";
import NFTTile from "./NFTTile";
import "../css/Profile.css";
import logo from "../비담 로고.png";
import { GetIpfsUrlFromPinata } from "./utils";
import PersonSVG from "./common/svg/PersonSVG";

// Profile은 사용자의 지갑 주소와 사용자가 가지고 있는 NFT들을 보여주는 페이지입니다.
export default function Profile() {
  const [data, updateData] = useState([]);
  const [dataFetched, updateFetched] = useState(false);
  const [address, updateAddress] = useState("0x");

  // 사용자가 가지고 있는 NFT들의 정보를 가져오는 함수입니다.
  async function getNFTData() {
    // 블록체인에 연결하기 위한 코드입니다.
    const ethers = require("ethers");

    const provider = new ethers.BrowserProvider(window.ethereum);
    const signer = await provider.getSigner();

    const addr = await signer.getAddress();
    const contractAddress = "0x295BC890632A6935691802A2F8d70E038c4F713f";
    const contractABI = require("../NFT.json");

    let contract = new ethers.Contract(
      contractAddress,
      contractABI,
      signer
    );

    //addr에 해당하는 사용자가 가지고 있는 모든 NFT들의 정보를 가져옵니다.
    let transaction = await contract.getMyNFTs(addr);

    // 가져온 NFT들의 정보를 JSON 형식에 담은 뒤, items에 넣습니다.
    const items = await Promise.all(
      transaction.map(async (i) => {
        let tokenURI = await contract.tokenURI(i.tokenId);
        tokenURI = GetIpfsUrlFromPinata(tokenURI);
        let meta = await axios.get(tokenURI);
        meta = meta.data;

        let item = {
          tokenId: Number(i.tokenId),
          owner: i.owner,
          image: meta.image,
          name: meta.name,
          description: meta.description,
        };
        return item;
      })
    );

    // items 안에 있는 정보들을 data(array type)에 업데이트합니다.
    updateData(items);
    updateFetched(true);
    updateAddress(addr);
  }

  // getNFTData 함수를 실행시킵니다.
  if (!dataFetched) getNFTData();

  return (
    <div className="flex items-center flex-col  bg-cream100 min-h-screen">
      {/* <Link to="/"><img src={logo} className='logo'/></Link>
        <Link to="/mint"><div className='back'>⚒️</div></Link>
        <Link to="/marketplace"><div className='back'>🛒</div></Link> */}
      <div className="flex lg:flex-row flex-col items-center justify-center  lg:mt-[60px]  mt-[30px] p-3">
        <div className="flex">
          <PersonSVG width="52px" height="52px" />
          <h3 className="lg:text-[32px]  text-[28px]  text-brand100 font-semibold ml-2">
            MY PROFILE
          </h3>
        </div>

        <div className="h-[70px] w-[1px] border border-black m-10 lg:block hidden"></div>
        <div className="flex  flex-col ">
          {/* 사용자의 지갑 주소와 가지고 있는 NFT의 갯수를 보여주는 부분입니다. */}
          <div className=" flex lg:flex-row flex-col w-fit  lg:mt-0 mt-10 bg-white border border-brown100 p-6 rounded-xl bg-opacity-70">
            <div>
              <h2 className="profile-info-box">Wallet Address</h2>
              <h3 className="profile-info-box-content">{address}</h3>
            </div>
            <div className="lg:ml-10">
              <h2 className="profile-info-box">No. of NFTs</h2>
              <h3 className="profile-info-box-content ">{data.length}</h3>
            </div>
          </div>
        </div>
      </div>

      <hr className="border border-mono60 lg:w-[70%] w-[90%] mt-[36px] lg:mb-10 mb-2" />

      {/* 사용자가 가지고 있는 NFT들을 보여주는 부분입니다 - NFTTile의 형식으로 보여줍니다(간단하게) */}
      <div className="lg:mt-10  mt-5 w-full flex items-center justify-center flex-col">
        <h2 className="text-brand100 lg:text-[28px] font-semibold lg:block hidden">
          YOUR NFT's
        </h2>
        <h2 className="text-white bg-black border border-[#0AFF96] rounded-2xl text-center w-[50%] p-1 text-[18px] font-semibold lg:hidden block">
          YOUR NFT's
        </h2>
        <div className="mt-2 text-pink100 lg:text-xl text-[12px]">
          {data.length == 0
            ? "Oops, No NFT data to display (Are you logged in?)"
            : ""}
        </div>
      </div>

      <div className="flex items-center justify-center">
        <div className="lg:grid lg:grid-cols-3 flex flex-col  items-center justify-center">
          {data.map((value, index) => {
            return <NFTTile data={value} key={index}></NFTTile>;
          })}
        </div>
      </div>
    </div>
  );
}
